import React from 'react';
import { IPrecioBitcoin } from '../interfaces';

export interface IPrecioProps {
  precioBitcoin: IPrecioBitcoin;
}
const Precio: React.FunctionComponent<IPrecioProps> = ({ precioBitcoin }) => {
  return <div>{precioBitcoin.price}</div>;
};

export default Precio;
